.main-page {
  display: contents;
}

.scroll-container {
  margin-top: auto;
  margin-bottom: auto;
  height: 470px;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;

  @include tablet {
    height: 900px;
  }

  @include medium-tablet {
    height: 100%;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 20px;

    @include medium-tablet {
      display: none;
    }
  }
}

.politika,
.soglashenie,
.faq {
  margin-top: auto;
  margin-bottom: auto;

  @include mobile {
    padding-top: 100px;
  }
}

.scroll-container-content {
  scroll-snap-align: center;
}

.scroll-container-content {
  padding-right: 40px;

  @include medium-tablet {
    padding-right: 0;
  }
}

.scroll-container-title {
  font-family: 'Gilroy', sans-serif;
  font-size: 32px;
  font-weight: 600;
  color: var(--light-color);
  margin: 0;
  padding: 0;
  margin-bottom: 25px;

  @include small-tablet {
    font-size: 26px;
  }

  @include mobile {
    font-size: 20px;
  }
}

.scroll-container-text {
  font-size: 15px;
  font-weight: 400;
  line-height: 21px;
  color: var(--light-color);
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
}

.uppercase {
  text-transform: uppercase;
}

.accent-color {
  color: var(--accent-color);
}

.semibold {
  font-weight: 600;
}

.link {
  color: var(--accent-color);
  transition: color 0.3s ease-in, text-decoration 0.3s ease-in;

  &:hover {
    color: var(--accent-darkColor);
    text-decoration: underline;
  }
}
