.faq {
  margin-top: 10px;
  margin-bottom: 40px;

  @include mobile {
    margin-bottom: 20px;
  }
}

.link-white {
  color: var(--light-color);
  text-decoration: underline;
  transition: text-decoration 0.3s ease-in;
  &:hover {
    text-decoration: none;
  }
}

// 

.accordion__list {
  margin-left: auto;
  margin-right: auto;
  width: 780px;

  @include medium-tablet {
    width: 100%;
  }
}

.accordion {
  margin-bottom: 10px;
}

.accordion__control {
  display: flex;
  align-items: center;
  padding: 12px 15px 12px 10px;
  border-radius: 12px;
  border: 1px solid rgba(134, 133, 138, 0.4);
  width: 100%;
  background-color: transparent;
  cursor: pointer;

  @include small-tablet {
    padding: 9px 10px 9px 8px;
  }

  @include mobile {
    padding: 8px 10px 8px 5px;
  }
}

.accordion__control::-webkit-details-marker {
  display: none;
}

.accordion__content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 0 0 60px;
  will-change: max-height;
  transition: all 0.3s ease-out;
  box-sizing: content-box;

  @include small-tablet {
    padding: 0 0 0 55px;
  }

  @include mobile {
    padding: 0;
  }
}

.accordion__title {
  font-size: 19px;
  font-weight: 400;
  line-height: 114%;
  text-align: left;
  color: var(--light-color);

  @include small-tablet {
    font-size: 17px;
  }

  @include mobile {
    font-size: 15px;
  }
}

.accordion__icon {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  min-width: 40px;
  min-height: 40px;

  @include small-tablet {
    margin-right: 7px;
  }

  @include mobile {
    min-width: 30px;
    min-height: 30px;
    margin-right: 5px;
  }
}

.accordion__icon::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 1.5px;
  background-color: var(--accent-color);
  transform: translate(-50%, -50%);
}

.accordion__icon::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 1.5px;
  height: 50%;
  background-color: var(--accent-color);
  transform: translate(-50%, -50%);
}

.accordion__icon {
  transition: transform 0.3s ease-out;
}

.open .accordion__icon {
  transform: rotate(45deg);
}

.open {
  .accordion__control {
    border: 1px solid rgba(134, 133, 138, 1);
  }
  .accordion__content {
    opacity: 1;
    padding: 20px 20px 10px 60px;
    margin-bottom: 10px;

    @include small-tablet {
      padding: 15px 10px 10px 55px;
    }

    @include mobile {
      padding: 15px 0 15px 0;
    }
  }
  .accordion__content-text {
    font-size: 14px;
    font-weight: 300;
    line-height: 150%;
    color: var(--light-color);
  }
  .accordion__title {
    color: var(--light-color);
  }
}
